.TodoItem {
  background-color: #FAFAFA;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;border: 2px 2px 2px 2px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
   border-radius: 90% ;
  border-color: #0000;
  
}

.TodoItem-p {
  color: #1E1E1F;
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
  border-radius: 80% ;
  border-color: #0000;
  border: 10px 2px 2px 2px;
}
.TodoItem-p--complete {
  text-decoration: line-through;
   border-color: rgba(221, 51, 51, 0);
   border: 5px 5px 2px 2px;
}
.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check:hover {
  color: rgb(38, 0, 255);
}
.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  top: -24px;
  right: 0;
}
.Icon-delete:hover {
  color: red;
}